.chatOnlineFriend{
    display: flex;
    align-items: center;
    font-weight: 500;
    cursor: pointer;
    margin-top: 10px;
}
.chatOnlineImgContainer{
    position: relative;
    margin-right: 10px;

}
.chatOnlineImg{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    object-fit: cover;
    border: 1px solide white;
}
.chatOnlineBadge{
    width: 10px;
    height: 10px;
    background-color: limegreen;
    border-radius: 50%;
    position: absolute;
    top:2px;
    right: 2px;
}
